.navigation {
  position: relative;
  z-index: 1;
  padding: 1.5rem 0;
  background-color: $color-brand-opaque;

  @media (min-width: $mobile) {
    padding: 2rem 0;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: baseline;

    &.active {
      & .navigation__list {
        display: flex;
      }

      & .navigation__line {
        &--1 {
          transform: rotate(45deg) translate(20%, -15%);
        }

        &--2 {
          opacity: 0;
        }

        &--3 {
          transform: rotate(-45deg) translate(-45%,-5%);
        }
      }
    }
  }

  &__list {
    @include ListReset();
    display: none;

    flex-wrap: wrap;
    flex-direction: column;

    width: 100%;
    margin-left: auto;
    margin-right: 0;

    @media (max-width: $mobile) {
      z-index: 1;
      position: absolute;
      left: 0;
      top: 0;
      width: 100vw;
      height: calc(100vh + 2rem);
      min-height: 100vh;
      background-color: $color-brand-menu;
      padding: 2rem 2.5rem;
    }
  }

  &__link {
    position: relative;
    display: block;

    padding: 1em;

    font-family: inherit;
    font-size: 18px;

    line-height: 100%;
    color: $default-color;
    text-decoration: none;

    white-space: nowrap;

    &:focus,
    &:hover {
      opacity: 0.6;
    }

    &:active::before {
      background-color: $default-color;
    }
  }

  &__toggle {
    display: none;
    z-index: 5;
    position: relative;
    top: 8px;
    right: 0;
    padding: 0;
    width: 30px;
    height: 25px;

    border: none;
    border-radius: 0;
    background-color: transparent;
    cursor: pointer;

    @media (max-width: $mobile) {
      display: flex;
      order:2;
    }
  }

  &__icon {
    fill: $default-color;
    width: 100%;
    height: 100%;
  }
}
